* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

title {
  display: none;
}

body {
  color: #212121;
  background-color: #131415;
  font-family: Roboto, Raleway, sans-serif;
}

.header {
  background-color: #1c1e1f;
  justify-content: center;
  align-items: center;
  padding: 8px 20px;
  display: flex;
}

.search-form {
  min-width: 185px;
  width: 70%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.search-form__input {
  height: 20px;
  color: #fcfeff;
  max-width: 300px;
  width: 100%;
  background: #2c3032;
  border: none;
  border-radius: 20px;
  outline: 1px solid #1f9ede00;
  padding: 20px 49px 20px 22px;
  transition: all .55s;
  display: block;
}

.search-form__input:focus, .search-form__input:hover {
  outline: 1px solid #1f9ede;
  transition: all .55s;
}

.search-form__input:not(:placeholder-shown) + .search-form__button {
  background-color: #1f9ede;
  transition: all .55s;
}

.search-form__button {
  width: 31px;
  height: 31px;
  min-width: 31px;
  cursor: pointer;
  background: none;
  border: none;
  border-radius: 50%;
  outline: 1px solid #1f9ede00;
  justify-content: center;
  align-items: center;
  transition: all .55s;
  display: flex;
  position: relative;
  right: 36px;
}

.search-form__button:focus {
  outline: 1px solid #1f9ede;
  transition: all .55s;
}

.search-form__icon {
  fill: #60676a;
  stroke-width: 1px;
  transition: all .55s;
}

.search-form__input:not(:placeholder-shown) + .search-form__button .search-form__icon {
  fill: #fcfeff;
  transition: all .55s;
}

